import React from 'react'



function Card({gambargelang, judul, subjudul, harga, isDisplay, position}) {

    


  return (
    <div className={isDisplay?`max-w-52 w-full bg-white shadow-lg rounded-lg overflow-hidden pb-0 absolute ${position} z-10 font-OpenSans`:"hidden"}>
        <div className={''}>
          <img className="w-36 h-auto mx-auto pt-3 pb-2" src={gambargelang} alt="gelang" />
        </div>
        
        <div className="flex flex-col text-left px-3 pt-2 pb-3">
        <div className='flex flex-row mb-2 justify-between items-center'>
            <div className="font-bold">{judul}</div>
            <div className="font-semibold text-sm text-black opacity-50">{harga}</div>
        </div>
            <div className="text-black text-xs opacity-50 text-justify">{subjudul}</div>
        </div>

          
    </div>

  )
}

export default Card