import React from 'react';
import Container from 'react-bootstrap/Container';
import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';
import '../App.css';


import gelang1 from '../images/1.png'
import gelang2 from '../images/2.png'
import gelang3 from '../images/3.png'
import gelang4 from '../images/4.png'
import gelang5 from '../images/5.png'
import gelang6 from '../images/6.png'
import gelang7 from '../images/7.png'
import gelang8 from '../images/8.png'
import Logo from '../images/logo_askaranna.png'

import Motif1 from '../images/batik.png'
import Motif2 from '../images/motif_batik_kanan2.png'

import {Link} from 'react-scroll'



function Home() {
  return (
    <div className='bg-gradient-to-b from-beige px-16 pt-4' id='home'>
        
        <div className='absolute top-0 right-0 -z-10 opacity-10'>
            <img src={Motif2} alt="Motif" className='w-[32rem]'/>
            {/* <img src={Motif1} alt="Motif" className=''/> */}
        </div>
        


        <nav className="flex flex-row justify-between items-center z-10 ">
            <a href='/' className="navbar-logo hover:cursor-pointer">
                <img src={Logo} alt="Logo" className='w-20'/>
            </a>

            
            <ul className="flex flex-row gap-16 text-lg">
                <li><Link to="home" spy={true} smooth={true} offset={50}  className="text-black no-underline decoration-4 decoration-orange hover:underline hover:underline-offset-8 hover:cursor-pointer">Home</Link></li>
                <li><Link to="product" spy={true} smooth={true} offset={50}  className="text-black no-underline decoration-4 decoration-orange hover:underline hover:underline-offset-8 hover:cursor-pointer">Product</Link></li>
                <li><Link to="team" spy={true} smooth={true} offset={50}   className="text-black no-underline decoration-4 decoration-orange hover:underline hover:underline-offset-8 hover:cursor-pointer">Team</Link></li>
                <li><Link to="contact" spy={true} smooth={true} offset={50} className="text-black no-underline decoration-4 decoration-orange hover:underline hover:underline-offset-8 hover:cursor-pointer">Contact</Link></li>
            </ul>
        </nav>

        <div className='grid grid-cols-2 gap-12'>
            <div className='mt-16'>
                <h1 className="font-Raleway font-bold text-6xl">Better Bracelets, Better Lifestyle</h1>
                <div className="opacity-50 mt-4 font-OpenSans">PT Askaranna is a company engaged in the fashion industry, specializing in the production and offering of ten variants of bracelets that combine modern style with Indonesian tradition.</div>
           

                <button className="cta-button bg-orange text-white py-2 px-4 rounded-full mt-4 font-OpenSans border-2 border-orange hover:bg-transparent hover:text-black">MORE INFO</button>

                

            </div>

            <div className='mt-16'>
              <Carousel
                indicators={true}
                data-bs-theme="dark"
                className='w-full'
                controls={false}
              >
                <Carousel.Item interval={2000}>
                  <img src={gelang1} alt="First slide" className='w-100'/>
                </Carousel.Item>
                <Carousel.Item interval={2000}>
                  <img src={gelang2} alt="Second slide" className='w-100'/>
                </Carousel.Item>
                <Carousel.Item interval={2000}>
                  <img src={gelang3} alt="Third slide" className='w-100'/>
                </Carousel.Item>
                <Carousel.Item interval={2000}>
                  <img src={gelang4} alt="Fourth slide" className='w-100'/>
                </Carousel.Item>
                <Carousel.Item interval={2000}>
                  <img src={gelang5} alt="Fifth slide" className='w-100'/>
                </Carousel.Item>
                <Carousel.Item interval={2000}>
                  <img src={gelang6} alt="Sixth slide" className='w-100'/>
                </Carousel.Item>
                <Carousel.Item interval={2000}>
                  <img src={gelang7} alt="Seventh slide" className='w-100'/>
                </Carousel.Item>
                <Carousel.Item interval={2000}>
                  <img src={gelang8} alt="Eighth slide" className='w-100'/>
                </Carousel.Item>
              </Carousel>
            </div>
            
        </div>
        
      
    
    </div>
  );
}

export default Home;