import React from 'react'



function Profile({gambar, nama}) {

    


  return (
    <div className={'max-w-52 w-full bg-white shadow-lg rounded-lg pb-0 font-OpenSans divide-y-2'}>
        <div className=''>
            <img className="object-fit mx-auto" src={gambar} alt="gelang" />
        </div>

        <div className="font-semibold text-center p-2 text-sm">{nama}</div>      
    </div>

  )
}

export default Profile


