import React from 'react';
import Container from 'react-bootstrap/Container';
import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';
import '../App.css';

import Profile from '../component/profile'

import logo from '../images/logo_askaranna.png'
import instagram from '../images/instagram.png'
import tiktok from '../images/tiktok.png'
import gelang2 from '../images/gelang2.png'



function Footer() {
  return (
    <div className='py-8 px-12 bg-black' id='contact'>
    
        <div className='flex flex-row justify-between mb-4'>
            <div>
                <img src={logo} alt="Logo" className='w-32'/>
            </div>
            <div className='flex flex-col gap-3 max-w-[22rem] text-beige'>
                <div>CONTACT US</div>
                <div>Sektor I.2, Jl. Letnan Sutopo, Rw. Buntu, Kec. Serpong, Kota Tangerang Selatan, Banten 15310</div>
                <div>+62-895-3749-65527</div>
            </div>
            <div>
            <button className="bg-orange text-white py-2 px-4 rounded-full mt-4 font-OpenSans border-2 border-orange hover:bg-transparent hover:text-orange">MORE INFO</button>

            </div>
        </div>

        <div className="border-t-2 border-beige w-full my-4 mb-4" />
       
        <div className='flex flex-col gap-3'>

            <div className='mx-auto flex flex-row gap-4'>
                <div className='bg-white rounded-full p-3 hover:opacity-50 hover:cursor-pointer'>
                    <a href='https://www.instagram.com/askaranna_/'>
                        <img src={instagram} alt='instagram' className='w-8'/>
                    </a>
                </div>
                <div className='bg-white rounded-full p-3 hover:opacity-50 hover:cursor-pointer'>
                    <a href='https://www.tiktok.com/@askaranna'>
                        <img src={tiktok} alt='instagram' className='w-8'/>
                    </a>
                </div>
            </div>
            
            <div className='text-center text-beige'> Askaranna 2024. All Rights Reserved</div>

        </div>


       

    </div>
  );
}

export default Footer;